  <div>
    <el-dialog v-model="upDingImgVisible" title="上传钉钉二维码" width="30%">
      <el-upload
        v-model:file-list="fileList"
        :action="UploadUrl()"
        :before-upload="checkFile"
        :on-success="UploadSuccess"
        :on-error="UploadFail"
        :data="upLoadData"
        :limit="1"
        accept=".bmp, .jpg, .jpeg, .gif, .png, .webp"
      >
        <el-tag>只可上传一张格式为JPG、PNG、JPEG的图片, 且不大于10M</el-tag>
        <el-divider direction="vertical" />
        <el-button size="small" type="primary">点击上传</el-button>
      </el-upload>
