  <div>
    <el-dialog v-model="changePassWordVisible" title="修改密码" width="30%">
      <el-form :model="changePassWordForm" label-width="120px">
        <el-form-item label="账号">
          <el-input v-model="changePassWordForm.id" :disabled="true" />
        </el-form-item>

        <el-form-item label="旧密码">
          <el-input v-model="changePassWordForm.oldPassword" />
        </el-form-item>

        <el-form-item label="新密码">
          <el-input v-model="changePassWordForm.newPassword" />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="ChangePassWord()">修改</el-button>
          <el-button @click="changePassWordVisible = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import router from "@/router";
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { Iphone, Location, Tickets, User } from "@element-plus/icons-vue";
import { ElMessage } from "element-plus";

export default {
  name: "TeacherInformationList",
  components: { Iphone, Location, Tickets, User },
  setup() {
    const state = reactive({
      fileList: [],
      teacherData: {},
      followStudentSize: 20,
      size: "large",
      // marginMap: {    large: '32px',default: '28px',small: '24px',},
      teacherFromVisible: false,
      teacherPaperFromVisible: false,
      teacherModData: {},
      changePassWordVisible: false,
      changePassWordForm: {
        id: "",
        newPassword: "",
        oldPassword: "",
        type: "1",
      },
      url: getCurrentInstance()?.appContext.config.globalProperties.$imageUrl,
      uploadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$uploadUrl,
      downloadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$downloadUrl,

      dingDingImgVisible: false,
      upDingImgVisible: false,
      upLoadData: {
        Id: "",
        grade: "",
        specialities: "",
        stuClass: "",
        type: "100",
      },
      checkFile: (val) => {
        if (val.size / 1048576 > 10) {
          ElMessage({
            message: "超出大小限制!",
            showClose: true,
            grouping: true,
            duration: 2000,
            type: "warning",
          });
          return false;
        }
        let mayFile = ".bmp, .jpg, .jpeg, .gif, .png, .webp";
        if (mayFile.indexOf(val.name.split(".")[1]) != -1) {
          return true;
        } else {
          ElMessage({
            message: "文件格式有误!",
            showClose: true,
            grouping: true,
            duration: 2000,
            type: "warning",
          });
          return false;
        }
      },
      queryTeacherData() {
        let teacherId = localStorage.getItem("id");
        axios({
          url: "/teacher/get/one?teacherId=" + teacherId,
        })
          .then(function (resp) {
            state.teacherData = resp.data;
            state.upLoadData.Id = resp.data.id;
            state.changePassWordForm.id = resp.data.id;
            state.teacherModData = JSON.parse(JSON.stringify(resp.data));
          })
          .catch((error) => {
            ElMessage({
              message: "请求教师数据失败!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      ChangePassWord() {
        axios({
          method: "post",
          url: "/edit/pwd",
          data: state.changePassWordForm,
        })
          .then(function (resp) {
            if (resp.data == "修改成功") {
              ElMessage({
                message: resp.data,
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
              });
              localStorage.removeItem("id");
              localStorage.removeItem("token");
              localStorage.removeItem("type");
              localStorage.removeItem("name");
              router.push("/");
            } else {
              ElMessage({
                message: resp.data,
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "网络异常，请刷新后重试!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },

      teacherModFromVisible(flag) {
        if (flag == 1) {
          state.teacherFromVisible = true;
        } else if (flag == 2) {
          state.teacherPaperFromVisible = true;
        }
      },
      queryModTeacher() {
        state.teacherFromVisible = false;
        state.teacherPaperFromVisible = false;
        axios({
          method: "put",
          url: "teacher",
          data: state.teacherModData,
        })
          .then(function (resp) {
            state.queryTeacherData();
            if (resp.data == true) {
              ElMessage({
                message: "修改成功!",
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
              });
            } else {
              ElMessage({
                message: "修改失败，请刷新页面后重试!",
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "修改失败，请刷新页面后重试! " + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },

      DingImgUrl() {
        let url = state.teacherData.image;
        let formatUrl = url.replace(/\\/g, "/");
        return state.downloadUrl + "/" + formatUrl;
      },
      UploadUrl() {
        return state.uploadUrl + "/upload";
      },
      UploadSuccess() {
        state.queryTeacherData();
        ElMessage({
          message: "上传成功!",
          showClose: true,
          grouping: true,
          duration: 1000,
          type: "success",
        });
      },
      UploadFail(error) {
        state.queryTeacherData();
        ElMessage({
          message: "上传失败" + error,
          showClose: true,
          grouping: true,
          duration: 2000,
          type: "error",
        });
      },
    });

    onMounted(() => {
      state.queryTeacherData();
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>


<style scoped>
.el-descriptions {
  margin-top: 20px;
}
.cell-item {
  display: flex;
  align-items: center;
}
.margin-top {
  margin-top: 20px;
}
</style>