    <!-- 列表 -->
    <el-table
      :data="studentList"
      stripe
      style="width: 100%"
      @selection-change="handleSelectionChangeButton"
    >
      <el-table-column type="selection" width="55"></el-table-column>

      <el-table-column type="index" align="center" width="60" label="序号" />

