    </div>

    <div>
      <el-dialog v-model="exportView" title="信息导出" width="30%">
        <el-form :model="exportForm" label-width="120px">
          <el-form-item label="">
            <el-input v-model="exportForm.tips" :disabled="true" />
          </el-form-item>

          <el-form-item label="导师姓名" >
            <el-input v-model="exportForm.teacherName" style="width: 50% "/>
            <el-divider direction="vertical" border-style="dashed" />
            <el-tag>注意名字不要有空格</el-tag>
          </el-form-item>

          <el-form-item label="班级">
            <el-select v-model="exportForm.stuClass" placeholder="请选择">
              <el-option label="不选择" value="" />
              <el-option
                label="2019级软件工程本科1班"
                value="2019级软件工程本科1班"
              />
              <el-option
                label="2019级软件工程本科2班"
                value="2019级软件工程本科2班"
              />
              <el-option
                label="2021级软件工程专升本1班"
                value="2021级软件工程专升本1班"
              />
              <el-option
                label="2021级软件工程专升本2班"
                value="2021级软件工程专升本2班"
              />

              <el-option
                label="2021级软件工程专升本3班"
                value="2021级软件工程专升本3班"
              />

              <el-option
                label="2021级软件工程专升本4班"
                value="2021级软件工程专升本4班"
              />
              <el-option
                label="2021级软件工程专升本5班"
                value="2021级软件工程专升本5班"
              />
            </el-select>
          </el-form-item>

