<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessage } from "element-plus";

export default {
  name: "StudentList",
  setup() {
    const state = reactive({
      slogan: "学生列表",
      downloadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$downloadUrl,
      searchData: {
        studentId: "",
        name: "",
        title: "",
        tutorFalg: "",
        stuClass: "",
        teacherName: "",
      },
      studentList: [],
      // studentTotal: 20,
      multipleSelectionData: [],
      page: { currentPage: 1, pageSize: 25, studentCount: 0 },
      formatSelect: {
        allSelect: 0,
        notSelect: 0,
        okSelect: 0,
        notSelectName: "",
        okSelectName: "",
        okSelectId: "",
      },
      teacherQuota: 0,
      followStudentSize:
        getCurrentInstance()?.appContext.config.globalProperties.$teacherStNum,
      userType: 0, //userType
      tempStudentData: {},
      adminModStudentVisible: false,
      paperFormVisible: false,
      exportView: false,
      exportForm: {
        teacherName: "",
        stuClass: "",
        tips: "您可以按导师、班级导出学生信息，若两者都不选，则导出所有学生!",
      },

      ExportFormButton: () => {
        let flag = confirm("文件正在生成，请注意浏览器下载提示!");
        if (flag) {
          axios({
            url:
              "/excel/getAll?teacherName=" +
              state.exportForm.teacherName +
              "&stuClass=" + state.exportForm.stuClass,
          })
            .then(function (resp) {
          
              alert("导出成功, 请注意浏览器下载提示!")
              let fileUrl = state.downloadUrl + resp.data;
              window.open(fileUrl, "_blank");
            })
            .catch((error) => {
              ElMessage({
                message: "导出失败, 请刷新后重试!" + error,
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "error",
              });
            });
        }
      },

      queryStudentList() {
        axios({
          method: "get",
          url:
            "/student/search?name=" +
            state.searchData.name +
            "&studentId=" +
            state.searchData.studentId +
            "&stuClass=" +
            state.searchData.stuClass +
            "&teacherName=" +
            state.searchData.teacherName +
            "&title=" +
            state.searchData.title +
            "&startIndex=" +
            state.page.currentPage +
            "&pageSize=" +
            state.page.pageSize,
        })
          .then(function (resp) {
            // console.log(resp)
            state.page.studentCount = resp.data["count"];
            state.studentList = resp.data["student"];
            ElMessage({
              message: "查询成功！",
              showClose: true,
              grouping: true,
              duration: 1000,
              type: "success",
            });
          })
          .catch((error) => {
            // console.log('查询老师失败!')
            ElMessage({
              message: "查询学生失败，请联系管理员!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      revokeTutor: () => {
        let flag = confirm(
          "确定要撤销 [" + state.tempStudentData.name + "] 的第一导师吗?"
        );
        if (flag) {
          axios({
            url: "/teacher/refuse?studentId=" + state.tempStudentData.studentId,
          })
            .then(function (resp) {
              state.adminModStudentVisible = false;
              state.queryStudentList();
              state.queryTeacherData();
              if (resp.data == true) {
                ElMessage({
                  message: "撤销成功!",
                  showClose: true,
                  grouping: true,
                  duration: 1000,
                  type: "success",
                });
              }
            })
            .catch((error) => {
              state.queryStudentList();
              ElMessage({
                message: "出现异常,请刷新页面后重试!" + error,
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "error",
              });
            });
        }
      },
      emptyClick() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
      },
      resetList() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
        state.queryStudentList();
      },

      handleSelectionChangeButton(val) {
        state.multipleSelectionData = val;
        // ElMessageBox.confirm(val)
      },
      multipleSelectButton() {
        //判断一下，是一个的话才传给后端
        let starr = state.multipleSelectionData;
        state.queryTeacherData();
        if (starr.length > state.followStudentSize) {
          alert("选择不能超过" + state.followStudentSize + "个!");
          return;
        } else if (
          starr.length >
          state.followStudentSize - state.teacherQuota
        ) {
          alert("选择数量大于当前剩余名额！");
          return;
        } else if (starr.length <= 0) {
          return;
        }

        let okSelect = 0;
        let okSelectName = "[";
        let okSelectId = "";
        for (let i = 0; i < starr.length; i++) {
          if (starr[i]["mentorId"] == 0) {
            okSelect = okSelect + 1;
            okSelectName = okSelectName + starr[i]["name"] + ",";
            okSelectId = okSelectId + starr[i]["studentId"] + ",";
          }
        }
        okSelectName = okSelectName + "]";
        if (okSelect != 0) {
          let teacherId = localStorage.getItem("id");
          let flag = confirm(
            "确定要将 " +
              okSelectName +
              okSelect +
              " 人加入到你的小组吗？ 未显示的学生表示已有导师，不能选择!"
          );
          if (flag) {
            axios({
              url:
                "/teacher/select?teacherId=" +
                teacherId +
                "&studentId=" +
                okSelectId,
            })
              .then(function (resp) {
                state.queryTeacherData();
                state.queryStudentList();
                if (resp.data == true) {
                  alert("添加成功！");
                } else {
                  alert(
                    "添加失败，可能是因为已添加学生数大于你的剩余学生名额，此时会部分添加成功！"
                  );
                }
              })
              .catch((error) => {
                alert("网络异常，请联系管理员!" + error);
              });
          }
        } else if (starr.length != 0) {
          confirm("这些人已经有导师了！");
        }
      },
      queryTeacherData() {
        let teacherId = localStorage.getItem("id");
        axios({
          url: "/teacher/get/one?teacherId=" + teacherId,
        })
          .then(function (resp) {
            state.teacherQuota = resp.data["studentNum"];
          })
          .catch((error) => {
            alert("教师数据请求异常!" + error);
          });
      },

      handleSizeChange(val) {
        state.page.pageSize = val;
        state.queryStudentList();
      },
      handleCurrentChange(val) {
        state.page.currentPage = val;
        state.queryStudentList();
      },
      adminModStudentFrom(st, flag) {
        if (flag == 1) {
          state.adminModStudentVisible = true;
        } else if (flag == 2) {
          state.paperFormVisible = true;
        }
        state.tempStudentData = JSON.parse(JSON.stringify(st));
      },
      queryModData() {
        state.adminModStudentVisible = false;
        state.paperFormVisible = false;
        axios({
          method: "put",
          url: "/student",
          data: state.tempStudentData,
        })
          .then(function (resp) {
            state.queryStudentList();
            if (resp.data == true) {
              ElMessage({
                message: "修改成功!",
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
              });
            } else {
              ElMessage({
                message: "修改失败，请刷新页面后重试!",
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "网络异常，请刷新页面后重试! " + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
    });

    onMounted(() => {
      state.queryStudentList();
      state.queryTeacherData();
      state.userType = localStorage.getItem("type");
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>



