<template>
  <div class="common-layout">
    <el-container>
      <!-- <el-header>Header</el-header> -->

      <el-container>
        <el-aside width="200px">
          <el-menu
            active-text-color="#ffd04b"
            background-color="#545c64"
            class="el-menu-vertical-demo"
            default-active="1"
            text-color="#fff"
            @open="handleOpen"
            @close="handleClose"
          >
            <!-- <el-menu-item index="0" @click="menuButtom">
                            <el-icon>
                                <icon-menu />
                            </el-icon>
                            <span>{{teacherData.name}} + "老师"</span>
                        </el-menu-item> -->

            <el-menu-item index="1" @click="menuButtom">
              <el-icon>
                <icon-menu />
              </el-icon>
              <span>主页-教师端</span>
            </el-menu-item>

            <el-menu-item index="2" @click="menuButtom">
              <el-icon>
                <User/>
              </el-icon>
              <span>学生列表</span>
            </el-menu-item>

                 <el-menu-item index="5" @click="menuButtom">
              <el-icon>
                <UserFilled />
              </el-icon>
              <span>教师列表</span>
            </el-menu-item>

            <el-menu-item index="3" @click="menuButtom">
              <el-icon>
                <User />
              </el-icon>
              <span>我的学生</span>
            </el-menu-item>

            <el-menu-item index="4" @click="menuButtom">
              <el-icon>
                <Comment />
              </el-icon>
              <span>学生申请</span>
            </el-menu-item>

                <el-menu-item index="6" @click="menuButtom">
              <el-icon>
                <BellFilled />
              </el-icon>
              <span>公告/帮助</span>
            </el-menu-item>

            <el-menu-item index="10" @click="menuButtom">
              <el-icon>
                <SwitchButton />
              </el-icon>
              <span>退出登录</span>
            </el-menu-item>
          </el-menu>
        </el-aside>

        <el-main>
          <!-- 1=主页， 2=所有学生，3=我的学生 -->
          <TeacherInformationList v-if="pageShow == 1"></TeacherInformationList>
          <StudentList v-else-if="pageShow == 2"></StudentList>
          <TeacherHomeStudentList
            v-else-if="pageShow == 3"
          ></TeacherHomeStudentList>
          <StudentApplicationList
            v-else-if="pageShow == 4"
          ></StudentApplicationList>
          <TeacherList v-else-if="pageShow == 5"/>
        </el-main>
      </el-container>

      <!-- <el-footer>Footer</el-footer> -->
    </el-container>
  </div>
</template>
  
  
<script>
import axios from "axios";
import { reactive, toRefs, onMounted } from "vue";
import router from "@/router";
import StudentList from "@/components/StudentList.vue";
import TeacherHomeStudentList from "@/components/teacherHome/TeacherHomeStudentList.vue";
import StudentApplicationList from "@/components/teacherHome/StudentApplicationList.vue";
import TeacherInformationList from "@/components/teacherHome/TeacherInformationList.vue";
import TeacherList from "@/components/TeacherList.vue"

import { Menu as IconMenu, User, SwitchButton, BellFilled, UserFilled,Comment } from "@element-plus/icons-vue";

export default {
  name: "teacherView",
  components: {
    StudentList,
    TeacherHomeStudentList,
    StudentApplicationList,
    TeacherInformationList,
    TeacherList,

    IconMenu,
    User,
    SwitchButton,BellFilled,UserFilled,Comment
  },

  setup() {
    const state = reactive({
      pageShow: 1,
      teacherData: {},
      followStudentSize: 20,

      // handleOpen(key, keyPath) {
      //     console.log('Open',key, keyPath)
      // },
      // handleClose(key, keyPath) {
      //     console.log('close',key, keyPath)
      // },

      menuButtom(val) {
        // 2=所有学生 ，3=退出登录
        if (val.index == 10) {
          localStorage.removeItem("id");
          localStorage.removeItem("token");
          localStorage.removeItem("type");
          localStorage.removeItem("name");
          router.push("/");
          return;
        } else if(val.index == 6){
            window.open("https://www.baidu.com/", "_blank");
        }else {
          state.pageShow = val.index;
        }
      },

      queryTeacherData() {
        let teacherId = localStorage.getItem("id");
        axios({
          url: "/teacher/get/one?teacherId=" + teacherId,
        })
          .then(function (resp) {
            state.teacherData = resp.data;
          })
          .catch(() => {
            alert("教师数据请求异常！");
          });
      },
    });

    onMounted(() => {
      // state.queryTeacherData()
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>
  

