<template>
  <div class="common-layout">
    <el-container>
      <!-- <el-header>
        header
        <BannerTop/>
      </el-header> -->

      <el-container>
        <el-aside width="200px">
          <el-menu
            active-text-color="#ffd04b"
            background-color="#545c64"
            class="el-menu-vertical-demo"
            default-active="1"
            text-color="#fff"
            @open="handleOpen"
            @close="handleClose"
          >
            <el-menu-item index="1" @click="menuButtom">
              <el-icon>
                <icon-menu />
              </el-icon>
              <span>主页-学生端</span>
            </el-menu-item>

            <el-menu-item index="2" @click="menuButtom">
              <el-icon>
                <Avatar />
              </el-icon>
              <span>导师列表</span>
            </el-menu-item>

            <el-menu-item index="3" @click="menuButtom">
              <el-icon>
                <FolderChecked />
              </el-icon>
              <span>文件中心</span>
            </el-menu-item>

            <el-menu-item index="4" @click="menuButtom">
              <el-icon>
                <BellFilled />
              </el-icon>
              <span>公告/帮助</span>
            </el-menu-item>

            <el-menu-item index="10" @click="menuButtom">
              <el-icon>
                <SwitchButton />
              </el-icon>
              <span>退出登录</span>
            </el-menu-item>
          </el-menu>
        </el-aside>

        <el-main>
          <StudentPersonalList v-if="viewPage == 1" />
          <TeacherList v-else-if="viewPage == 2" />
          <FileCenter v-else-if="viewPage == 3" />
        </el-main>
      </el-container>

      <el-footer>
        <BottomStyle />
      </el-footer>
    </el-container>
  </div>
</template>
  
  
<script>
import { reactive, toRefs } from "vue";
import router from "@/router";
import TeacherList from "@/components/TeacherList.vue";
import StudentPersonalList from "@/components/studentHome/StudentPersonalList.vue";
import FileCenter from "@/components/studentHome/FileCenter.vue";
// import BannerTop from '@/components/component/BannerTop.vue'
import BottomStyle from "@/components/component/BottomStyle.vue";
import {
  Menu as IconMenu,
  Avatar,
  SwitchButton,
  FolderChecked,
  BellFilled,
} from "@element-plus/icons-vue";

export default {
  name: "StudentView",
  components: {
    TeacherList,
    FileCenter,
    StudentPersonalList,
    BottomStyle,

    IconMenu,
    Avatar,
    SwitchButton,
    FolderChecked,
    BellFilled,
  },

  setup() {
    const state = reactive({
      viewPage: 1,

      // handleOpen(key, keyPath) {
      //     console.log('Open',key, keyPath)
      // },
      // handleClose(key, keyPath) {
      //     console.log('close',key, keyPath)
      // },

      menuButtom(val) {
        if (val.index == 10) {
          localStorage.removeItem("id");
          localStorage.removeItem("token");
          localStorage.removeItem("type");
          localStorage.removeItem("name");
          router.push("/");
        }else if(val.index == 4){
          window.open("https://www.baidu.com/", "_blank");
        } else {
          state.viewPage = val.index;
        }
      },
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>


  

