<template>
  <div id="pageView">
    <el-form :inline="true" :model="searchData" class="demo-form-inline">
      <!-- <el-form-item label="工号">
                <el-input v-model="searchData.jobNo" placeholder="工号" />
            </el-form-item> -->

      <el-form-item label="姓名">
        <el-input v-model="searchData.name" placeholder="姓名" />
      </el-form-item>

      <!-- <el-form-item label="论文题目">
                <el-input v-model="searchData.title" placeholder="论文题目" />
            </el-form-item> -->

      <el-form-item>
        <el-button type="primary" @click="queryTeacherList()">搜索</el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="emptyClick()">清空</el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="resetList()">重置列表</el-button>
      </el-form-item>

      <el-form-item v-if="userType == 2">
        <el-button type="danger">请选择一位老师成为你的导师！</el-button>
      </el-form-item>

      <el-form-item v-if="userType == 2">
        <el-button type="danger">
          不可申请表示你已有导师，或有正在申请的导师
        </el-button>
      </el-form-item>

      <!-- 该按钮只有是管理员的时候才显示 -->
      <el-form-item>
        <el-button
          type="primary"
          v-if="userType == 3"
          @click="multipleSelectButton()"
          >选择</el-button
        >
      </el-form-item>
    </el-form>

    <!-- 多选提示框 -->
    <el-dialog
      v-model="dialogVisible"
      title="Tips"
      width="30%"
      :before-close="handleClose"
    >
      <span>This is a message</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="dialogVisible = false"
            >Confirm</el-button
          >
        </span>
      </template>
    </el-dialog>

    <!-- 列表 -->
    <el-table
      :data="teacherList"
      stripe
      style="width: 100%"
      @selection-change="handleSelectionChangeButton"
    >
      <el-table-column type="selection" width="55"></el-table-column>

      <el-table-column type="index" align="center" width="60" label="序号" />

      <el-table-column align="center" prop="name" label="姓名" />

      <el-table-column align="center" prop="id" label="工号" />

      <el-table-column align="center" prop="studentNum" label="已选人数" />

      <el-table-column align="center" prop="major" label="专业" />

      <el-table-column align="center" prop="academicDegree" label="学位" />

      <el-table-column align="center" prop="teacherTitle" label="职称" />

      <el-table-column align="center" prop="phone" label="手机" />

      <el-table-column align="center" prop="email" label="邮箱" />

      <el-table-column align="center" label="剩余名额">
        <template #default="scope">
          <el-button type="success">{{
            studentTotal - scope.row.studentNum
          }}</el-button>

          <!--   -->
          <el-button
            type="primary"
            v-if="
              studentTotal - scope.row.studentNum > 0 &&
              currentStudent.mentorId == 0 &&
              userType == 2
            "
            @click="studentSelectStudent(scope.row)"
            >申请</el-button
          >

          <el-button
            type="warning"
            v-else-if="currentStudent.mentorId != 0 && userType == 2"
          >
            不可申请
          </el-button>

          <el-button type="warning" v-else-if="userType == 2">已满</el-button>
        </template>
      </el-table-column>

      <!-- <el-table-column prop="address" label="Address" /> -->
    </el-table>

    <el-pagination
      v-model:currentPage="page.currentPage"
      v-model:page-size="page.pageSize"
      :page-sizes="[20, 50, 80, 120]"
      :small="small"
      :disabled="disabled"
      :background="background"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.teacherCount"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>


<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessageBox, ElMessage } from "element-plus";

export default {
  name: "TeacherList",
  setup() {
    const state = reactive({
      msg: "信工学院",
      searchData: { id: "", name: "", title: "" },
      dialogVisible: false,
      teacherList: [],
      currentStudent: {},
      studentTotal:
        getCurrentInstance()?.appContext.config.globalProperties.$teacherStNum,
      multipleSelectionData: [],
      page: { currentPage: 1, pageSize: 25, teacherCount: 0 },
      userType: 0, //2表示学生 3表示管理员 1表示教师

      queryTeacherList() {
        //?name &  startIndex & pageSize
        axios({
          method: "get",
          url:
            "/teacher?name=" +
            state.searchData.name +
            "&startIndex=" +
            state.page.currentPage +
            "&pageSize=" +
            state.page.pageSize,
        })
          .then(function (resp) {
            // console.log(resp)

            ElMessage({
              message: "查询成功!",
              showClose: true,
              grouping: true,
              duration: 1000,
              type: "success",
            });
            state.page.teacherCount = resp.data["count"];
            state.teacherList = resp.data["teacher"];
            // state.page.teacherCount = resp.data['count']
          })
          .catch((error) => {
            ElMessage({
              message: "查询老师失败!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      emptyClick() {
        state.searchData.id = "";
        state.searchData.name = "";
        state.searchData.title = "";
      },
      resetList() {
        state.searchData.id = "";
        state.searchData.name = "";
        state.searchData.title = "";
        state.queryTeacherList();
      },

      handleSelectionChangeButton(val) {
        state.multipleSelectionData = val;
        // ElMessageBox.confirm(val)
      },
      multipleSelectButton() {
        ElMessageBox.confirm(state.multipleSelectionData);
        //判断一下，是一个的话才传给后端
      },

      handleSizeChange(val) {
        state.page.pageSize = val;
        state.queryTeacherList();
      },
      handleCurrentChange(val) {
        state.page.currentPage = val;
        state.queryTeacherList();
      },
      studentSelectStudent(teacher) {
        let stId = localStorage.getItem("id");
        let tId = teacher.id;

        axios({
          url: "/student/select?teacherId=" + tId + "&studentId=" + stId,
        })
          .then(function (resp) {
            state.queryTeacherList();
            if (resp.data == true) {
              ElMessage({
                message: "申请成功，请等待老师同意!",
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
              });
            } else {
              ElMessage({
                message: "申请失败，请刷新后重试!",
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "网络异常，请刷新后重试!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
    });

    onMounted(() => {
      state.queryTeacherList();
      state.userType = localStorage.getItem("type");

      if (state.userType == 2) {
        let stId = localStorage.getItem("id");
        axios({
          url:
            "/student/search?name=" +
              "&stuClass=" +
            "&teacherName=" +
            "&studentId=" +
            stId +
            "&title=" +
            
            "&startIndex=1&pageSize=25",
        })
          .then(function (resp) {
            state.currentStudent = resp.data["student"][0];
          })
          .catch((error) => {
            ElMessage({
              message: "请求学生信息失败，请刷新后重试!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      }
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>



<style>
.dialog-footer button:first-child {
  margin-right: 10px;
}
</style>