<template>
  <div>
    <div>
      <el-alert
        title="上传后刷新页面，显示的文件名没有改变，说明格式不对，没有上传成功! "
        type="warning"
        show-icon
      />
      <br />
      <el-alert
        title="支持的上传类型如下: bmp, jpg, jpeg, gif, png, webp, doc, docx, pdf, zip, rar; 且大小不可超过600M。 "
        type="warning"
        show-icon
      />
      <br />
      <el-alert
        title="上传文件名字请不要有空格； 重新上传将会覆盖原有文件！"
        type="warning"
        show-icon
      />
      <br />
      <el-alert
        title="重新上传文件后，上次批语仍会保留，直到导师发送新的批语!"
        type="warning"
        show-icon
      />
    </div>

    <div>
      <el-descriptions
        class="margin-top"
        title=""
        :column="3"
        :size="large"
        border
      >
        <!-- <template #extra>
          <el-button type="primary">Operation</el-button>
        </template> -->
        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              文件1
            </div>
          </template>
          <el-tag
            class="ml-2"
            type="warning"
            v-if="
              currentStudent.file == null || currentStudent.file.length == 0
            "
          >
            未提交
          </el-tag>
          <el-tag class="ml-2" type="success" v-else>
            {{ formatFileUrl(currentStudent.file) }}
          </el-tag>
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              导师批语1
            </div>
          </template>
          <el-tag
            class="ml-2"
            type="warning"
            v-if="
              currentStudent.notes3 == null || currentStudent.notes3.length == 0
            "
          >
            暂无批语
          </el-tag>
          <el-tag class="ml-2" type="success" v-else>
            {{ currentStudent.notes3 }}
          </el-tag>
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              操作
            </div>
          </template>
          <el-button type="success" @click="DownFile(1)">下载</el-button>
          <el-button type="danger" @click="UpLoadType(1)">重新上传</el-button>
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              文件2
            </div>
          </template>
          <el-tag
            class="ml-2"
            type="warning"
            v-if="
              currentStudent.file2 == null || currentStudent.file2.length == 0
            "
          >
            未提交
          </el-tag>
          <el-tag class="ml-2" type="success" v-else>
            {{ formatFileUrl(currentStudent.file2) }}
          </el-tag>
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              导师批语2
            </div>
          </template>
          <el-tag
            class="ml-2"
            type="warning"
            v-if="
              currentStudent.notes4 == null || currentStudent.notes4.length == 0
            "
          >
            暂无批语
          </el-tag>
          <el-tag class="ml-2" type="success" v-else>
            {{ currentStudent.notes4 }}
          </el-tag>
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              操作
            </div>
          </template>
          <el-button type="success" @click="DownFile(2)">下载</el-button>
          <el-button type="danger" @click="UpLoadType(2)">重新上传</el-button>
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              文件3
            </div>
          </template>
          <el-tag
            class="ml-2"
            type="warning"
            v-if="
              currentStudent.file3 == null || currentStudent.file3.length == 0
            "
          >
            未提交
          </el-tag>
          <el-tag class="ml-2" type="success" v-else>
            {{ formatFileUrl(currentStudent.file3) }}
          </el-tag>
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              导师批语3
            </div>
          </template>
          <el-tag
            class="ml-2"
            type="warning"
            v-if="
              currentStudent.notes5 == null || currentStudent.notes5.length == 0
            "
          >
            暂无批语
          </el-tag>
          <el-tag class="ml-2" type="success" v-else>
            {{ currentStudent.notes5 }}
          </el-tag>
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <user />
              </el-icon>
              操作
            </div>
          </template>
          <el-button type="success" @click="DownFile(3)">下载</el-button>
          <el-button type="danger" @click="UpLoadType(3)">重新上传</el-button>
        </el-descriptions-item>
      </el-descriptions>
    </div>

    <div>
      <el-dialog v-model="upLoadVisible" title="文件上传" width="30%">
        <el-upload
          v-model:file-list="fileList"
          :action="UploadUrl()"
          :before-upload="checkFile"
          :on-success="UploadSuccess"
          :on-error="UploadFail"
          :data="upLoadData"
          :limit="1"
          accept=".bmp, .jpg, .jpeg, .gif, .png, .webp, .doc, .docx, .pdf, .zip, .rar"
        >
          <el-tag>{{ upLoadTitle }}</el-tag>
          <el-divider direction="vertical" border-style="dashed" />
          <el-button size="small" type="primary">点击上传</el-button>
        </el-upload>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessage } from "element-plus";
import { User } from "@element-plus/icons-vue";
export default {
  components: {
    User,
  },
  setup() {
    const state = reactive({
      fileList: [],
      uploadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$uploadUrl,
      downloadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$downloadUrl,
      upLoadData: {
        Id: "",
        grade: "",
        specialities: "",
        stuClass: "",
        type: "",
      },
      currentStudent: {},
      upLoadVisible: false,
      upLoadTitle: "",

      UploadUrl() {
        return state.uploadUrl + "/upload";
      },
      checkFile(val) {
        if (val.size / 1048576 > 600) {
          ElMessage({
            message: "超出大小限制!",
            showClose: true,
            grouping: true,
            duration: 2000,
            type: "warning",
          });
          return false;
        }
        let mayFile =
          ".bmp, .jpg, .jpeg, .gif, .png, .webp, .doc, .docx, .pdf, .zip, .rar";
        if (mayFile.indexOf(val.name.split(".")[1]) != -1) {
          return true;
        } else {
          ElMessage({
            message: "文件格式有误!",
            showClose: true,
            grouping: true,
            duration: 2000,
            type: "warning",
          });
          return false;
        }
      },
      UploadSuccess() {
        state.queryStudentData();
        ElMessage({
          message: "上传成功!",
          showClose: true,
          grouping: true,
          duration: 1000,
          type: "success",
        });
      },
      UploadFail(error) {
        state.queryStudentData();
        ElMessage({
          message: "上传失败! " + error,
          showClose: true,
          grouping: true,
          duration: 2000,
          type: "error",
        });
      },
      UpLoadType(flag) {
        state.fileList = [];
        if (flag == 1) {
          state.upLoadData.type = 1;
          state.upLoadTitle = "上传文件1";
        } else if (flag == 2) {
          state.upLoadData.type = 2;
          state.upLoadTitle = "上传文件2";
        } else if (flag == 3) {
          state.upLoadData.type = 3;
          state.upLoadTitle = "上传文件3";
        }
        state.upLoadVisible = true;
      },
      DownFile(flag) {
        let fileUrl = "";
        if (flag == 1) {
          fileUrl = state.currentStudent.file.replace(/\\/g, "/");
        } else if (flag == 2) {
          fileUrl = state.currentStudent.file2.replace(/\\/g, "/");
        } else if (flag == 3) {
          fileUrl = state.currentStudent.file3.replace(/\\/g, "/");
        }
        let formatUrl = state.downloadUrl + "/" + fileUrl;
        // console.log(formatUrl)
        window.open(formatUrl, "_blank");
      },

      queryStudentData() {
        let stId = localStorage.getItem("id");
        axios({
          url:
            "/student/search?name=" +
             "&stuClass=" +
            "&teacherName=" +
            "&studentId=" +
            stId +
            "&title=" +
            
            "&startIndex=1&pageSize=25",
        })
          .then(function (resp) {
            state.currentStudent = resp.data["student"][0];
            state.upLoadData.Id = resp.data["student"][0]["studentId"];
            state.upLoadData.grade = resp.data["student"][0]["grade"];
            state.upLoadData.specialities =
              resp.data["student"][0]["specialities"];
            state.upLoadData.stuClass = resp.data["student"][0]["stuClass"];
          })
          .catch((error) => {
            ElMessage({
              message: "请求学生信息失败，请刷新后重试!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },

      formatFileUrl: (fileUrl) => {
        let formatU = fileUrl.split("\\");
        let Ulength = formatU.length;
        return formatU[Ulength - 1];
      },
    });

    onMounted(() => {
      state.queryStudentData();
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>


<style scoped>
.el-descriptions {
  margin-top: 20px;
}
.cell-item {
  display: flex;
  align-items: center;
}
.margin-top {
  margin-top: 20px;
}
</style>