import { createRouter, createWebHashHistory } from 'vue-router'
import { ElMessage } from "element-plus";
import HomeView from '../views/HomeView.vue'
import StudentList from '../components/StudentList.vue'
import TeacherList from '../components/TeacherList.vue'

import TeacherView from '../views/TeacherView.vue'
import TeacherHomeStudentList from '../components/teacherHome/TeacherHomeStudentList.vue'
import StudentApplicationList from '../components/teacherHome/StudentApplicationList.vue'
import TeacherInformationList from '../components/teacherHome/TeacherInformationList.vue'
import FileCenter from '../components/studentHome/FileCenter.vue'

import LoginView from '../views/LoginView.vue'

import StudentView from '../views/StudentView.vue'
import StudentPersonalList from '../components/studentHome/StudentPersonalList.vue'

import ThanksDevelopers from '../components/about/ThanksDevelopers.vue'

import MyTestView from '../views/MyTestView.vue'

const routes = [{
    path: '/home',
    name: 'home',
    component: HomeView,
},
{
    path: '/:catchAll(.*)',
    name: '404',
    component: () =>
        import('@/components/NotFound.vue'),
    meta: {
        title: 404
    }

},
{
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
        import( /* webpackChunkName: "about" */ '../views/AboutView.vue')
},
{
    path: '/studentList',
    name: 'studentList',
    component: StudentList,
    meta: {
        title: '学生列表',
    }
},
{
    path: '/myTestView',
    name: 'myTestView',
    component: MyTestView,
    meta: {
        title: '测试页',
    }
},
{
    path: '/teacherList',
    name: 'teacherList',
    component: TeacherList,
    meta: {
        title: '教师列表',
    }
},
{
    path: '/',
    name: 'loginView',
    component: LoginView,
    meta: {
        title: '登录',
    }
},
{
    path: '/teacherView',
    name: 'teacherView',
    component: TeacherView,
    meta: {
        title: '教师后台',
    }
},
{
    path: '/studentView',
    name: 'studentView',
    component: StudentView,
    meta: {
        title: '学生后台',
    }
},
{
    path: '/teacherHomeStudentList',
    name: 'teacherHomeStudentList',
    component: TeacherHomeStudentList,
    meta: {
        title: '我的学生',
    }
},
{
    path: '/studentApplicationList',
    name: 'studentApplicationList',
    component: StudentApplicationList,
    meta: {
        title: '学生申请',
    }
},
{
    path: '/teacherInformationList',
    name: 'teacherInformationList',
    component: TeacherInformationList,
    meta: {
        title: '教师后台信息页',
    }
},
{
    path: '/studentPersonalList',
    name: 'studentPersonalList',
    component: StudentPersonalList,
    meta: {
        title: '学生后台信息页'
    }
},
{
    path: '/thanksDevelopers',
    name: 'thanksDevelopers',
    component: ThanksDevelopers,
    meta: {
        title: '开发者'
    }
},
{
    path: '/fileCenter',
    name: 'fileCenter',
    component: FileCenter,
    meta: {
        title: '文件中心'
    }
},
]

const router = createRouter({
    history: createWebHashHistory(),
    base: '/dist',
    routes
})

router.beforeEach((to, form, next) => {
    //home页随后删掉或者也屏蔽掉



    //鉴权放在这里，没登录的话只能跳到登录页 或者 404页 ，登录过的话直接跳到对应后台
    if (to.name == 'loginView' || to.name == '404' || to.name == 'thanksDevelopers' || to.name == 'myTestView') {
        document.title = to.meta.title
        next()
    }
    else if (to.name == 'teacherView' || to.name == 'studentView') {
        let token = localStorage.getItem("token");
        let id = localStorage.getItem("id");
        let userName = localStorage.getItem("name");
        if (token != null && id != null) {
            ElMessage({
                message: userName + "，您已登录!",
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
            });
            document.title = to.meta.title
            next()
        }

    } else {
        document.title = to.meta.title
        next('/404')
    }


})

export default router