<template>
  <div id="pageView">
    <el-form :inline="true" :model="searchData" class="demo-form-inline">
      <!-- <el-form-item label="学号">
        <el-input v-model="searchData.studentId" placeholder="学号" />
      </el-form-item> -->

      <!-- <el-form-item label="姓名">
        <el-input v-model="searchData.name" placeholder="姓名" />
      </el-form-item> -->

      <!-- <el-form-item label="论文题目">
        <el-input v-model="searchData.title" placeholder="论文题目" />
      </el-form-item> -->

      <!-- <el-form-item>
        <el-button type="primary" @click="queryStudentList()">模糊查询</el-button>
      </el-form-item> -->

      <!-- <el-form-item>
        <el-button type="primary" @click="emptyClick()">清空</el-button>
      </el-form-item> -->

      <el-form-item>
        <el-button type="warning"
          >您有{{ followStudentSize }}个名额，已用{{ teacherQuota }}个，剩余{{
            followStudentSize - teacherQuota
          }}个</el-button
        >
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="resetList()">刷新列表</el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="multipleSelectButton()"
          >同意申请</el-button
        >
      </el-form-item>
    </el-form>

    <!-- 列表 -->
    <el-table
      :data="studentList"
      stripe
      style="width: 100%"
      @selection-change="handleSelectionChangeButton"
    >
      <el-table-column type="selection" width="55"></el-table-column>

      <el-table-column type="index" align="center" width="60" label="序号" />

      <el-table-column align="center" prop="name" label="姓名" />

      <el-table-column align="center" prop="studentId" label="学号" />

      <el-table-column align="center" prop="specialities" label="专业" />

      <el-table-column align="center" prop="stuClass" label="班级" />

      <el-table-column align="center" prop="phone" label="手机" />

      <el-table-column align="center" prop="email" label="邮箱" />

      <el-table-column align="center" label="状态">
        <template #default="scope">
          <el-button type="primary" v-if="scope.row.isSelected == 0"
            >未同意</el-button
          >
          <el-button type="success" v-else-if="scope.row.isSelected"
            >已同意</el-button
          >
          <el-button type="warning" @click="refuse(scope.row)">拒绝</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- <div>
        <el-pagination v-model:currentPage="page.currentPage" v-model:page-size="page.pageSize"
      :page-sizes="[20, 50, 80, 120]" :small="small" :disabled="disabled" :background="background"
      layout="total, sizes, prev, pager, next, jumper" :total="page.studentCount" @size-change="handleSizeChange"
      @current-change="handleCurrentChange" />
    </div> -->
  </div>
</template>


<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessage } from "element-plus";

export default {
  name: "StudentApplicationList",
  setup() {
    const state = reactive({
      slogan: "学生列表",
      searchData: { studentId: "", name: "", title: "", tutorFalg: "" },
      dialogVisible: false,
      studentList: [],
      // studentTotal: 20,
      multipleSelectionData: [],
      page: { currentPage: 1, pageSize: 25, studentCount: 0 },
      formatSelect: {
        allSelect: 0,
        notSelect: 0,
        okSelect: 0,
        notSelectName: "",
        okSelectName: "",
        okSelectId: "",
      },
      teacherQuota: 0,
      followStudentSize:
        getCurrentInstance()?.appContext.config.globalProperties.$teacherStNum,

      queryStudentList() {
        let teacherId = localStorage.getItem("id");
        axios({
          method: "get",
          url: "/teacher/student?teacherId=" + teacherId + "&isSelected=0",
        })
          .then(function (resp) {
            // console.log(resp)
            // state.page.studentCount = resp.data['count']
            // state.studentList = resp.data['student']
            state.studentList = resp.data;
            ElMessage({
              message: "查询成功!",
              showClose: true,
              grouping: true,
              duration: 1000,
              type: "success",
            });
          })
          .catch((error) => {
            // console.log('查询老师失败!')
            ElMessage({
              message: "查询学生失败!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      queryTeacherData() {
        let teacherId = localStorage.getItem("id");
        axios({
          url: "/teacher/get/one?teacherId=" + teacherId,
        })
          .then(function (resp) {
            state.teacherQuota = resp.data["studentNum"];
          })
          .catch((error) => {
            ElMessage({
              message: "请求教师数据异常!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },

      emptyClick() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
      },
      resetList() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
        state.queryStudentList();
      },

      handleSelectionChangeButton(val) {
        state.multipleSelectionData = val;
        // ElMessageBox.confirm(val)
      },
      multipleSelectButton() {
        //判断一下，是一个的话才传给后端
        let starr = state.multipleSelectionData;
        state.queryTeacherData();
        if (starr.length > state.followStudentSize) {
          alert("选择不能超过" + state.followStudentSize + "个!");
          return;
        } else if (
          starr.length >
          state.followStudentSize - state.teacherQuota
        ) {
          alert("选择数量大于当前剩余名额！");
          return;
        } else if (starr.length <= 0) {
          return;
        }

        let okSelectName = "[";
        let okSelectId = "";
        for (let i = 0; i < starr.length; i++) {
          okSelectName = okSelectName + starr[i]["name"] + ",";
          okSelectId = okSelectId + starr[i]["studentId"] + ",";
        }
        okSelectName = okSelectName + "]";

        let teacherId = localStorage.getItem("id");
        let flag = confirm(
          "确定要将 " +
            okSelectName +
            " 共 " +
            starr.length +
            " 人加入到你的小组吗？"
        );
        if (flag) {
          axios({
            url:
              "/teacher/select?teacherId=" +
              teacherId +
              "&studentId=" +
              okSelectId,
          })
            .then(function (resp) {
              state.queryTeacherData();
              state.queryStudentList();
              if (resp.data == true) {
                alert("添加成功!");
              } else {
                alert(
                  "添加失败，可能是因为已添加学生数大于你的剩余学生名额，此时会部分添加成功！"
                );
              }
            })
            .catch(() => {
              alert("异常，请联系管理员！");
            });

          // setTimeout(state.queryStudentList(), 3000);
        }
      },
      refuse(st) {
        let stName = st.name;
        let stId = st.studentId;
        if (confirm("确定要拒绝 [" + stName + " ] 的申请吗?")) {
          axios({
            url: "/teacher/refuse?studentId=" + stId,
          })
            .then(function (resp) {
              state.queryTeacherData();
              state.queryStudentList();
              if (resp.data == true) {
                ElMessage({
                  message: "拒绝成功!",
                  showClose: true,
                  grouping: true,
                  duration: 1000,
                  type: "success",
                });
              }
            })
            .catch((error) => {
              state.queryTeacherData();
              state.queryStudentList();
                 ElMessage({
                  message: "出现异常,请刷新页面后重试!" + error,
                  showClose: true,
                  grouping: true,
                  duration: 2000,
                  type: "error",
                });
            });
        }
      },

      handleSizeChange(val) {
        state.page.pageSize = val;
        state.queryStudentList();
      },
      handleCurrentChange(val) {
        state.page.currentPage = val;
        state.queryStudentList();
      },
    });

    onMounted(() => {
      state.queryStudentList();
      state.queryTeacherData();
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>



