<template>
  <div id="pageView">
    <div>
      <el-form :inline="true" :model="searchData" class="demo-form-inline">
        <!-- <el-form-item label="学号">
          <el-input v-model="searchData.studentId" placeholder="学号" />
        </el-form-item> -->

        <!-- <el-form-item label="姓名">
          <el-input v-model="searchData.name" placeholder="姓名" />
        </el-form-item> -->

        <!-- <el-form-item label="论文题目">
          <el-input v-model="searchData.title" placeholder="论文题目" />
        </el-form-item> -->

        <!-- <el-form-item>
          <el-button type="primary" @click="queryStudentList()"
            >模糊查询</el-button
          >
        </el-form-item> -->

        <!-- <el-form-item>
          <el-button type="primary" @click="emptyClick()">清空</el-button>
        </el-form-item> -->

        <el-form-item>
          <el-button type="warning">
            您有{{ followStudentSize }}个名额，已用{{ teacherQuota }}个，剩余{{
              followStudentSize - teacherQuota
            }}个</el-button
          >
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="resetList()">刷新列表</el-button>
        </el-form-item>

             <el-form-item>
          <el-button type="primary" @click="ExportFormButton()">导出学生信息</el-button>
        </el-form-item>

        <!-- <el-form-item>
          <el-button type="primary" @click="multipleSelectButton()"
            >选择</el-button
          >
        </el-form-item> -->
      </el-form>
    </div>

    <div>
      <el-table
        :data="studentList"
        stripe
        style="width: 100%"
        @selection-change="handleSelectionChangeButton"
      >
        <el-table-column type="selection" width="55"></el-table-column>

        <el-table-column type="index" align="center" width="55" label="序号" />

        <el-table-column align="center" prop="name" label="姓名" />

        <el-table-column align="center" prop="studentId" label="学号" />

        <el-table-column align="center" prop="specialities" label="专业" />

        <el-table-column align="center" prop="stuClass" label="班级" />

        <el-table-column align="center" prop="title" label="论文" />

        <el-table-column align="center" prop="phone" label="手机" />

        <!-- <el-table-column align="center" prop="email" label="邮箱" /> -->

        <el-table-column align="center" label="操作">
          <template #default="scope">
            <el-button type="primary" @click="teacherModStudent(scope.row, 1)"
              >论文/评分</el-button
            >
            <el-button type="primary" @click="teacherModStudent(scope.row, 2)"
              >文件</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 列表 -->

    <!-- <div>
      <el-pagination
        v-model:currentPage="page.currentPage"
        v-model:page-size="page.pageSize"
        :page-sizes="[20, 50, 80, 120]"
        :small="small"
        :disabled="disabled"
        :background="background"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.studentCount"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div> -->

    <div>
      <el-dialog v-model="studentFromVisible" title="论文信息" width="30%">
        <el-tag class="ml-2" type="success"
          >不可编辑的栏目说明不在开放期。</el-tag
        >

        <el-form :model="tempStudentFrom" label-width="120px">
          <el-form-item label="学号">
            <el-input v-model="tempStudentFrom.studentId" :disabled="true" />
          </el-form-item>

          <el-form-item label="姓名">
            <el-input v-model="tempStudentFrom.name" :disabled="true" />
          </el-form-item>

          <el-form-item label="导师1">
            <el-input v-model="tempStudentFrom.mentorId" :disabled="true" />
          </el-form-item>

          <el-form-item label="导师2">
            <el-input v-model="tempStudentFrom.mentor2Id" />
            <el-tag class="ml-2" type="success"
              >请第一导师为在此填写为学生指派的第二导师工号</el-tag
            >
            <el-tag class="ml-2" type="success"
              >在教师列表中可查看对应老师的工号。</el-tag
            >
          </el-form-item>

          <el-form-item label="是否完成实践">
            <el-select
              v-model="tempStudentFrom.isCompletePractice"
              placeholder="请选择"
            >
              <el-option label="1-是" value="1" />
              <el-option label="0-否" value="0" />
            </el-select>
            <el-tag class="ml-2" type="success">1-是，0-否</el-tag>
          </el-form-item>

          <el-form-item label="是否第一届毕业">
            <el-select
              v-model="tempStudentFrom.isFirstSession"
              placeholder="请选择"
            >
              <el-option label="1-是" value="1" />
              <el-option label="0-否" value="0" />
            </el-select>
            <el-tag class="ml-2" type="success">1-是，0-否</el-tag>
          </el-form-item>

          <el-form-item label="成果类别">
            <el-input v-model="tempStudentFrom.thesisType" :disabled="true"/>
          </el-form-item>

          <el-form-item label="论文题目">
            <el-input v-model="tempStudentFrom.title" />
          </el-form-item>

          <el-form-item label="论文语言">
            <el-select v-model="tempStudentFrom.language" placeholder="请选择">
              <el-option label="中文" value="中文" />
              <el-option label="English" value="English" />
            </el-select>
          </el-form-item>

          <el-form-item label="研究方向">
            <el-input v-model.number="tempStudentFrom.researchDirection" />
          </el-form-item>

          <el-form-item label="课题来源">
            <el-select
              v-model="tempStudentFrom.sourceTopicSelection"
              placeholder="请选择"
            >
              <el-option label="教师拟定" value="教师拟定" />
              <el-option label="学生自选" value="学生自选" />
              <el-option label="企业和社会征集" value="企业和社会征集" />
              <el-option label="科研单位提供" value="科研单位提供" />
            </el-select>
          </el-form-item>

          <el-form-item label="选题性质">
            <el-select
              v-model="tempStudentFrom.natureTopicSelection"
              placeholder="请选择"
              :disabled="true"
            >
              <el-option label="1-理论研究" value="1" />
              <el-option label="2-应用研究" value="2" />
              <el-option label="3-技术开发" value="3" />
              <el-option label="4-工程设计" value="4" />
            </el-select>
            <br />
            <el-tag class="ml-2" type="success">
              1-理论研究，2-应用研究，3-技术开发，4-工程设计</el-tag
            >
          </el-form-item>

          <el-form-item label="关键词">
            <el-input v-model.number="tempStudentFrom.thesisKeyword" />
          </el-form-item>

          <el-form-item label="论文重复率">
            <el-input
              v-model.number="tempStudentFrom.repetitionRate"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="指导教师评分">
            <el-input
              v-model.number="tempStudentFrom.mentorScore"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="评阅老师评分">
            <el-input
              v-model.number="tempStudentFrom.appraiserScore"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="答辩小组评分">
            <el-input
              v-model.number="tempStudentFrom.defenseScore"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="综合评分">
            <el-input
              v-model.number="tempStudentFrom.comprehensiveScore"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="评价等级">
            <el-select
              v-model="tempStudentFrom.evaluationGrade"
              placeholder="请选择"
              :disabled="isEdit"
            >
              <el-option label="1-优秀" value="1" />
              <el-option label="2-良好" value="2" />
              <el-option label="3-合格" value="3" />
              <el-option label="4-不合格" value="4" />
            </el-select>
            <br />
            <el-tag class="ml-2" type="success"
              >1-优秀，2-良好，3-合格，4-不合格</el-tag
            >
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="queryModStudentData()"
              >提交</el-button
            >
            <el-button @click="studentFromVisible = false">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

    <div>
      <el-dialog v-model="studenFileVisible" title="学生文件" width="30%">
        <el-tag class="ml-2" type="success" v-if="isEdit == false"
          >学生重新上传后，为方便记忆，上次批语仍会保留，请重新编辑批语！</el-tag
        >

        <el-form :model="tempStudentFrom" label-width="120px">
          <el-form-item label="学号">
            <el-input v-model="tempStudentFrom.studentId" :disabled="true" />
          </el-form-item>

          <el-form-item label="姓名">
            <el-input v-model="tempStudentFrom.name" :disabled="true" />
          </el-form-item>

          <el-form-item label="文件1">
            <el-divider direction="vertical" border-style="dashed" />
            <el-tag
              class="ml-2"
              type="warning"
              v-if="
                tempStudentFrom.file == null || tempStudentFrom.file.length == 0
              "
            >
              未提交
            </el-tag>

            <span v-else>
              <el-tag class="ml-2" type="success">{{
                tempStudentFrom.file.split("\\")[
                  tempStudentFrom.file.split("\\").length - 1
                ]
              }}</el-tag>
              <el-divider direction="vertical" border-style="dashed" />
              <el-button
                type="success"
                @click="DownStudentFile(1)"
                v-if="tempStudentFrom.file != null"
                >预览/下载</el-button
              >
            </span>
          </el-form-item>

          <el-form-item label="我的批语1">
            <el-input v-model="tempStudentFrom.notes3" type="textarea" />
          </el-form-item>

          <el-form-item label="文件2">
            <el-divider direction="vertical" border-style="dashed" />
            <el-tag
              class="ml-2"
              type="warning"
              v-if="
                tempStudentFrom.file2 == null ||
                tempStudentFrom.file2.length == 0
              "
            >
              未提交
            </el-tag>
            <span v-else>
              <el-tag class="ml-2" type="success">{{
                tempStudentFrom.file2.split("\\")[
                  tempStudentFrom.file2.split("\\").length - 1
                ]
              }}</el-tag>
              <el-divider direction="vertical" border-style="dashed" />
              <el-button type="success" @click="DownStudentFile(2)"
                >预览/下载</el-button
              >
            </span>
          </el-form-item>

          <el-form-item label="我的批语2">
            <el-input v-model="tempStudentFrom.notes4" type="textarea" />
          </el-form-item>

          <el-form-item label="文件3">
            <el-divider direction="vertical" border-style="dashed" />
            <el-tag
              class="ml-2"
              type="warning"
              v-if="
                tempStudentFrom.file3 == null ||
                tempStudentFrom.file3.length == 0
              "
            >
              未提交
            </el-tag>
            <span v-else>
              <el-tag class="ml-2" type="success">{{
                tempStudentFrom.file3.split("\\")[
                  tempStudentFrom.file3.split("\\").length - 1
                ]
              }}</el-tag>
              <el-divider direction="vertical" border-style="dashed" />
              <el-button type="success" @click="DownStudentFile(3)"
                >预览/下载</el-button
              >
            </span>
          </el-form-item>

          <el-form-item label="我的批语3">
            <el-input v-model="tempStudentFrom.notes5" type="textarea" />
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="queryModStudentData()"
              >提交</el-button
            >
            <el-button @click="studenFileVisible = false">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>



  </div>
</template>


<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessage } from "element-plus";

export default {
  name: "TeacherHomeStudentList",
  setup() {
    const state = reactive({
      uploadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$uploadUrl,
      downloadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$downloadUrl,
      slogan: "学生列表",
      searchData: { studentId: "", name: "", title: "", tutorFalg: "" },
      dialogVisible: false,
      studentList: [],
      // studentTotal: 20,
      multipleSelectionData: [],
      page: { currentPage: 1, pageSize: 25, studentCount: 0 },
      formatSelect: {
        allSelect: 0,
        notSelect: 0,
        okSelect: 0,
        notSelectName: "",
        okSelectName: "",
        okSelectId: "",
      },
      teacherQuota: 0,
      followStudentSize:
        getCurrentInstance()?.appContext.config.globalProperties.$teacherStNum,
      tempStudentFrom: {},
      studentFromVisible: false,
      isEdit: true,
      studenFileVisible: false,
      exportForm: {teacherName: '', stuClass: ''},

      DownStudentFile: (flag) => {
        let fileUrl = "";
        if (flag == 1) {
          fileUrl =
            state.downloadUrl +
            "/" +
            state.tempStudentFrom.file.replace(/\\/g, "/");
        } else if (flag == 2) {
          fileUrl =
            state.downloadUrl +
            "/" +
            state.tempStudentFrom.file2.replace(/\\/g, "/");
        } else if (flag == 3) {
          fileUrl =
            state.downloadUrl +
            "/" +
            state.tempStudentFrom.file3.replace(/\\/g, "/");
        }
        window.open(fileUrl, "_blank");
      },

      ExportFormButton: ()=>{
        state.exportForm.teacherName = localStorage.getItem("name");
        let flag = confirm("将导出您 [" + state.exportForm.teacherName  + "] 名下的所有学生的信息，请注意浏览器下载提示!")
        if(flag){
          axios({
            url: '/excel/getAll?teacherName=' + state.exportForm.teacherName + '&stuClass='
          }).then(function(resp){
          
            alert("导出成功, 请注意浏览器下载提示!")
            let fileUrl = state.downloadUrl + resp.data
            window.open(fileUrl, "_blank");
          }).catch((error)=>{
                 ElMessage({
              message: "导出失败, 请刷新后重试!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          })
        }
      },

      queryStudentList() {
        let teacherId = localStorage.getItem("id");
        axios({
          method: "get",
          url: "/teacher/student?teacherId=" + teacherId + "&isSelected=1",
        })
          .then(function (resp) {
            // console.log(resp)
            // state.page.studentCount = resp.data['count']
            // state.studentList = resp.data['student']
            state.studentList = resp.data;
            ElMessage({
              message: "查询成功！",
              showClose: true,
              grouping: true,
              duration: 1000,
              type: "success",
            });
          })
          .catch((error) => {
            ElMessage({
              message: "查询学生失败，请联系管理员！" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      emptyClick() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
      },
      resetList() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
        state.queryStudentList();
      },

      handleSelectionChangeButton(val) {
        state.multipleSelectionData = val;
        // ElMessageBox.confirm(val)
      },
      multipleSelectButton() {
        // confirm(state.multipleSelectionData[0]['name'])

        //判断一下，是一个的话才传给后端
        let starr = state.multipleSelectionData;

        if (starr.length > 20) {
          alert("选择不能超过20个！");
          return;
        }

        let okSelect = 0;
        let okSelectName = "[";
        let okSelectId = "";
        for (let i = 0; i < starr.length; i++) {
          if (starr[i]["mentorId"] == 0) {
            okSelect = okSelect + 1;
            okSelectName = okSelectName + starr[i]["name"] + ",";
            okSelectId = okSelectId + starr[i]["studentId"] + ",";
          }
        }

        okSelectName = okSelectName + "]";
        if (okSelect != 0) {
          let teacherId = localStorage.getItem("id");
          let flag = confirm(
            "确定要将 " +
              okSelectName +
              okSelect +
              " 人加入到你的小组吗？ 未显示的学生表示已有导师，不能选择!"
          );
          if (flag) {
            axios({
              url:
                "/teacher/select?teacherId=" +
                teacherId +
                "&studentId=" +
                okSelectId,
            })
              .then(function (resp) {
                if (resp.data == true) {
                  alert("添加成功！");
                } else {
                  alert(
                    "添加失败，可能是因为已添加学生数大于你的剩余学生名额，此时会部分添加成功！"
                  );
                }
              })
              .catch(() => {
                alert("异常，请联系管理员！");
              });
          }
        } else if (starr.length != 0) {
          confirm("这些人已经有导师了！");
        }
      },
      queryTeacherData() {
        let teacherId = localStorage.getItem("id");
        axios({
          url: "/teacher/get/one?teacherId=" + teacherId,
        })
          .then(function (resp) {
            state.teacherQuota = resp.data["studentNum"];
          })
          .catch((error) => {
            ElMessage({
              message: "请求教师数据失败!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },

      handleSizeChange(val) {
        state.page.pageSize = val;
        state.queryStudentList();
      },
      handleCurrentChange(val) {
        state.page.currentPage = val;
        state.queryStudentList();
      },

      teacherModStudent(st, flag) {
        state.tempStudentFrom = JSON.parse(JSON.stringify(st));
        if (flag == 1) {
          state.studentFromVisible = true;
        } else if (flag == 2) {
          state.studenFileVisible = true;
        }
      },
      queryModStudentData() {
        state.studentFromVisible = false;
        state.studenFileVisible = false;
        axios({
          method: "put",
          url: "/student",
          data: state.tempStudentFrom,
        })
          .then(function (resp) {
            state.queryStudentList();
            if (resp.data == true) {
              ElMessage({
                message: "修改成功!",
                showClose: true,
                grouping: true,
                duration: 1000,

                type: "success",
              });
            } else {
              ElMessage({
                message: "修改失败，请刷新页面后重试!",
                showClose: true,
                grouping: true,
                duration: 2000,

                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "修改失败，请刷新页面后重试! " + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
    });

    onMounted(() => {
      state.queryStudentList();
      state.queryTeacherData();
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>


<style scoped>
.el-alert {
  margin: 20px 0 0;
}
.el-alert:first-child {
  margin: 0;
}
</style>



